.logo.active .logo_image{
  opacity: 0;
  animation-name: logo;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards; 
}
@keyframes logo {
  from{opacity: 0;}
  to{opacity: 1;}
}


//  KAMPANA //
#slider_svg path {
  opacity: 0;
}
#slider_svg.active path:nth-child(1){
  stroke-dasharray: 1472px;
  stroke-dashoffset: 1472px;
  animation: svg_anim 2.5s ease forwards;
 }
 #slider_svg.active path:nth-child(2){
   stroke-dasharray: 1218px;
   stroke-dashoffset: 1218px;
   animation: svg_anim 2.5s ease forwards ;
 }
 #slider_svg.active path:nth-child(3){
   stroke-dasharray: 2073px;
   stroke-dashoffset: 2073px;
   animation: svg_anim 2.5s ease forwards ;
 }
 #slider_svg.active path:nth-child(4){
   stroke-dasharray: 1468px;
   stroke-dashoffset: 1468px;
   animation: svg_anim 2s ease-in forwards ;
 }
 #slider_svg.active path:nth-child(5){
   stroke-dasharray: 1218px;
   stroke-dashoffset: 1218px;
   animation: svg_anim 2s ease-in-out forwards ;
 }
 #slider_svg.active path:nth-child(6){
   stroke-dasharray: 1571px;
   stroke-dashoffset: 1571px;
   animation: svg_anim 2.5s ease forwards ;
 }
 #slider_svg.active path:nth-child(7){
   stroke-dasharray: 1218px;
   stroke-dashoffset: 1218px;
   animation: svg_anim 2.5s ease forwards ;
 }
 @keyframes svg_anim{
   from{opacity: 0;}
   to{
