//   calculation //

.heading_calculation{
  width: 300px;
  position: relative;
  top: 20px;
  right: 4%;
  font-size: 48.008px; 
  animation: svg_recipe 7s ease forwards;
  line-height: 3rem;
}
.calculation_img:nth-child(1){
  transform: translateX(424px);
  opacity: 0;
  
}
.calculation_content_wrapper.active .calculation_img:nth-child(1),.calculation_content_wrapper.active .calculation_img:nth-child(3)  {
  animation: svg_calc 2s ease forwards ;
}

.calculation_img:nth-child(3){
  transform: translateX(-424px); 
  opacity: 0;
  // animation: svg_calc 5s ease forwards;
}
@keyframes svg_calc{
  from{opacity: 0;}
  to{
    transform: translateX(0px);
    opacity: 1;
  }
}

