@keyframes toDrawBorder {
  0% {
      background-size:    0 1px,
                          1px 0,
                          0 1px,
                          1px 0;
  }
  25% {
      background-size:    100% 1px,
                          1px 0,
                          0 1px,
                          1px 0;
  }
  50% {
      background-size:    100% 1px,
                          1px 100%,
                          0 1px,
                          1px 0;
  }
  75% {
      background-size:    100% 1px,
                          1px 100%,
                          100% 1px,
                          1px 0;
  }
  100% {
      background-size:    100% 1px,
                          1px 100%,
                          100% 1px,
                          1px 100%;
  }
}



